html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img,
fieldset,
a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input,
button {
  margin: 0;
  padding: 0;
  border: 0;
}

div,
input,
textarea,
select,
button,
h1,
h2,
h3,
h4,
h5,
h6,
a,
span,
a:focus {
  outline: none;
}

ul,
ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

@font-face {
  font-family: "boldProxima";
  src: url("../fonts/ProximaNova-Bold.woff2") format("woff2"), url("../fonts/ProximaNova-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "lightProxima";
  src: url("../fonts/ProximaNova-Light.woff2") format("woff2"), url("../fonts/ProximaNova-Light.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "regularProxima";
  src: url("../fonts/ProximaNova-Regular.woff2") format("woff2"), url("../fonts/ProximaNova-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "OstrichSans-Black";
  src: url("../fonts/OstrichSans-Black.woff2") format("woff2"), url("../fonts/OstrichSans-Black.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

strong {
  font-family: boldProxima, sans-serif;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #fff;
}

.cover {
  width: 100%;
  min-height: 60rem;
  height: 100vh;
}

.cover__hero {
  height: 60rem;
}

.cover__img {
  margin: 0 auto;
  display: block;
  width: 42.553191489%;
  padding-top: 5rem;
}

.cover__skew {
  background-size: cover;
  background-position: center;
  box-shadow: inset 0px -3000px 0px rgba(0, 0, 0, 0.75);
  position: relative;
  z-index: 99;
  padding-bottom: 28rem;
  transform: skew(0, -10deg) translateY(-15rem);
  background-color: #2a323a;
}

.cover__content {
  transform: skew(0, 10deg) translateY(17rem);
}

.container {
  width: 100%;
  max-width: 98rem;
  margin: 0 auto;
  padding: 0 2rem;
}

.header__nav {
  padding-top: 5rem;
  width: 79.78723%;
  margin: 0 auto;
}

.header__logo a {
  font-family: boldProxima, sans-serif;
  text-transform: uppercase;
  color: #f39c12;
  font-size: 3.5rem;
  text-decoration: none;
}

.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.menu__link {
  font-family: boldProxima, sans-serif;
  color: white;
  opacity: .6;
  font-size: 1.4rem;
  text-decoration: none;
  text-transform: uppercase;
}

.menu__link:hover {
  color: red;
}

.submenu {
  display: flex;
  flex-direction: column;
}

.submenu__link {
  font-family: regularProxima, sans-serif;
  color: #fff;
  font-size: 1.6rem;
  line-height: 21px;
  text-decoration: none;
}

.submenu__link:hover {
  color: red;
}

.section {
  background-color: #fff;
}

.section__content {
  text-align: center;
  padding-top: 35rem;
  margin: 0 auto;
}

.section__title {
  font-family: "OstrichSans-Black", sans-serif;
  text-transform: uppercase;
  font-size: 3rem;
}

.article {
  margin: 0 auto 17.6rem;
  display: flex;
  align-items: center;
  width: 84.04255%;
}

.article__content {
  padding-top: 1.6rem;
  width: 58.98734%;
  font-size: 2.4rem;
  margin-right: 2.91139%;
}

.article__content--right {
  order: 1;
  margin-right: 0;
  margin-left: 2.91139%;
}

.article__title {
  font-size: 3.2rem;
  font-family: "OstrichSans-Black", sans-serif;
  text-transform: uppercase;
}

.article__text {
  padding-top: 1rem;
  font-family: regularProxima, sans-serif;
  font-size: 1.6rem;
  color: #222;
  line-height: 2.1rem;
}

.article figure {
  width: 37.97468%;
}

.article__img {
  width: 100%;
  display: block;
  background-color: #333;
}

.panel {
  background-color: #fff;
  position: relative;
  z-index: 99;
}

.panel__skew {
  background-size: cover;
  background-position: center;
  background-repeat: repeat-y;
  box-shadow: inset 100vmax 0 0 rgba(0, 0, 0, 0.75);
  position: relative;
  transform: skew(0, 5deg) translateY(10rem);
}

.panel__content {
  padding-top: 13.5rem;
  transform: skew(0, -5deg);
  padding-bottom: 5.5rem;
  text-align: center;
  font-size: 3rem;
  width: 85.95745%;
  margin: 0 auto;
}

.panel__text {
  color: rgba(255, 255, 255, 0.8);
  font-family: "Myriad Pro", sans-serif;
  font-size: 3rem;
  line-height: 3.6rem;
}

.panel__btn {
  margin-top: 2.5rem;
  display: inline-block;
  font-family: boldProxima, sans-serif;
  font-size: 2.4rem;
  color: #2ecc71;
  padding: 1.3rem 6.2rem;
  text-decoration: none;
  border: 3px solid #2ecc71;
  border-radius: 3rem;
  text-transform: uppercase;
  transition: .25s linear;
}

.panel__btn:hover {
  background-color: #2ecc71;
  color: #fff;
}

.reviews {
  position: relative;
  z-index: 9;
  text-align: center;
  padding-top: 5rem;
}

.reviews__skew {
  padding-bottom: 15rem;
  background-color: #fff;
  transform: skew(0, -6deg) translateY(13rem);
}

.reviews__content {
  transform: skew(0, 6deg);
}

.reviews__review {
  margin-bottom: 7.5rem;
}

.reviews__container {
  padding-top: 10rem;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.reviews__column {
  width: 42.55319%;
}

.review {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
}

.review__text {
  position: relative;
  font-family: regularProxima, sans-serif;
  font-size: 1.6rem;
  line-height: 2.1rem;
  padding-bottom: 2rem;
  border-bottom: 3px solid #a1a1a1;
}

.review__text:after {
  content: '';
  position: absolute;
  width: 3px;
  height: 2rem;
  background-color: #a1a1a1;
  right: 0;
  bottom: 0;
}

.review__user {
  margin-left: auto;
  margin-top: 2rem;
}

.review__user-photo {
  position: relative;
  vertical-align: middle;
  display: inline-block;
  width: 4.6rem;
  height: 4.6rem;
  background-size: cover;
  border-radius: 50%;
  background-position: center center;
}

.review__user-photo:before {
  content: '';
  width: 3rem;
  position: absolute;
  left: -2.5rem;
  top: -2.7rem;
  height: 1.5rem;
  background-color: #fff;
  border-right: 3px solid #a1a1a1;
  border-bottom: 3px solid #a1a1a1;
  transform: rotate(45deg) skew(50deg);
}

.review__username {
  font-family: boldProxima, sans-serif;
  display: inline-block;
  vertical-align: middle;
  font-size: 1.6rem;
}

.review__username span {
  margin-right: 0;
  font-family: regularProxima, sans-serif;
  font-size: 0.875em;
}

.footer {
  width: 100%;
  min-height: 700px;
  height: 100%;
  position: relative;
}

.footer__map {
  width: 100%;
  height: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  top: 0;
}

.footer__mask {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  background-color: rgba(42, 50, 58, 0.9);
}

.footer__content {
  padding-top: 29rem;
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  height: 100%;
}

.footer__column {
  width: 25.53191%;
  text-align: left;
  border-right: 1px dotted #a1a1a1;
}

.footer__column:nth-child(2) {
  text-align: center;
}

.footer__column--big {
  border: none;
  margin-left: auto;
  width: 40.42553%;
}

.footer__logo {
  font-family: "OstrichSans-Black", sans-serif;
  font-size: 2.4rem;
  text-transform: uppercase;
  color: #f39c12;
}

.footer__address {
  font-family: regularProxima, sans-serif;
  font-size: 1.6rem;
  padding: 25px 0;
  color: #fff;
  font-style: normal;
}

.footer__contact {
  font-family: regularProxima, sans-serif;
}

.footer__contact span {
  display: block;
  color: #fff;
  text-transform: uppercase;
}

.footer__contact a {
  padding-left: .5em;
  display: inline-block;
  text-decoration: none;
  color: #2ecc71;
  text-transform: none;
}

.footer__form {
  position: relative;
  padding-bottom: 30px;
}

.footer__btn-submit {
  background-color: transparent;
  position: absolute;
  display: inline-block;
  right: 0;
  top: 0;
  width: 15px;
  height: 15px;
  background-image: url(../images/i-arrow.svg);
  background-size: cover;
  background-position: center;
}

.footer__text {
  font-family: lightProxima, sans-serif;
  color: #fff;
  text-transform: uppercase;
  font-size: 1.6rem;
  line-height: 3rem;
}

.footer__copy {
  margin-top: auto;
  margin-bottom: 5rem;
  font-family: regularProxima, sans-serif;
  font-size: 1.4rem;
  color: #fff;
  order: 2;
}

.input {
  display: inline-block;
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #a1a1a1;
  padding-bottom: 5px;
  font-family: regularProxima, sans-serif;
  color: #fff;
}

.input:placeholder {
  font-family: regularProxima, sans-serif;
  text-transform: uppercase;
  color: rgba(1, 1, 1, 0.5);
  font-size: 1.6rem;
}

@media only screen and (max-width: 800px) {
  .header__logo {
    order: -1;
    width: 100%;
    text-align: center;
  }
}

@media only screen and (max-width: 780px) {
  html {
    font-size: 55%;
  }
}

@media only screen and (max-width: 768px) {
  .reviews__column {
    width: 100%;
  }

  .footer {
    min-height: 1000px;
  }

  .footer__content {
    padding-top: 17rem;
  }

  .footer__column {
    border: none;
    width: 70%;
    margin: 0 auto;
    text-align: center;
    padding-bottom: 5rem;
  }

  .footer__column:nth-child(2) {
    order: 1;
  }
}

@media only screen and (max-width: 750px) {
  .submenu {
    flex-direction: row;
    justify-content: space-between;
  }
}

@media only screen and (max-width: 700px) {
  .panel__content {
    width: 100%;
  }
}

@media only screen and (max-width: 650px) {
  .article {
    flex-direction: column;
    width: 100%;
    margin-bottom: 8rem;
  }

  .article__content {
    width: 100%;
    text-align: center;
    order: 1;
  }

  .article__text {
    width: 100%;
  }

  .article figure {
    width: 100%;
  }
}

@media only screen and (max-width: 600px) {
  .cover__img {
    width: 60%;
  }
}

@media only screen and (max-width: 550px) {
  .header__nav {
    width: 100%;
  }
}

@media only screen and (max-width: 500px) {
  .footer__column {
    width: 100%;
  }
}

@media only screen and (max-width: 400px) {
  .cover__img {
    width: 80%;
  }
}