@import "helpers/all";

@include font("boldProxima", ProximaNova-Bold);
@include font("lightProxima", ProximaNova-Light);
@include font("regularProxima", ProximaNova-Regular);
@include font("OstrichSans-Black", OstrichSans-Black);


@mixin black {
	font-family: "OstrichSans-Black", sans-serif; }
@mixin bold {
	font-family: boldProxima, sans-serif; }
@mixin light {
	font-family: lightProxima, sans-serif; }
@mixin regular {
	font-family: regularProxima, sans-serif; }
strong {
  @include bold; }
html {
  box-sizing: border-box;
  font-size: 62.5%;
  @include r(780) {
    font-size: 55%; } }
*, *:before, *:after {
  box-sizing: inherit; }
body {
  min-width: 320px;
  width: 100%;
  height: 100%;
  font-size: 1.6rem;
  background-color: #fff; }
.cover {
  width: 100%;
  min-height: 60rem;
  height: 100vh;
  &__hero {
    height: 60rem; }
  &__img {
    margin: 0 auto;
    display: block;
    width: 42.553191489%;
    padding-top: 5rem;
    @include r(600) {
      width: 60%; }
    @include r(400) {
      width: 80%; } }
  &__skew {
    background-size: cover;
    background-position: center;
    box-shadow: inset 0px -3000px 0px rgba(0, 0, 0, .75);
    position: relative;
    z-index: 99;
    padding-bottom: 28rem;
    transform: skew(0, -10deg) translateY(-15rem);
    background-color: #2a323a; }
  &__content {
    transform: skew(0, 10deg) translateY(17rem); } }
.container {
  width: 100%;
  max-width: 98rem;
  margin: 0 auto;
  padding: 0 2rem; }
.header {
  &__nav {
    padding-top: 5rem;
    @include w(750, 940);
    margin: 0 auto;
    @include r(550) {
      width: 100%; } }
  &__logo {
    @include r(800) {
      order: -1;
      width: 100%;
      text-align: center; }
    a {
      @include bold;
      text-transform: uppercase;
      color: #f39c12;
      font-size: 3.5rem;
      text-decoration: none; } } }
.menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  &__link {
    @include bold;
    color: white;
    opacity: .6;
    font-size: 1.4rem;
    text-decoration: none;
    text-transform: uppercase;
    &:hover {
      color: red; } } }
.submenu {
  display: flex;
  flex-direction: column;
  @include r(750) {
    flex-direction: row;
    justify-content: space-between; }
  &__link {
    @include regular;
    color: #fff;
    font-size: 1.6rem;
    line-height: 21px;
    text-decoration: none;
    &:hover {
      color: red; } } }
.section {
  background-color: #fff;
  &__content {
    text-align: center;
    padding-top: 35rem;
    margin: 0 auto; }
  &__title {
    @include black;
    text-transform: uppercase;
    font-size: 3rem; } }
.article {
  margin: 0 auto 17.6rem;
  display: flex;
  align-items: center;
  @include w(790, 940);
  @include r(650) {
    flex-direction: column;
    width: 100%;
    margin-bottom: 8rem; }
  &__content {
    padding-top: 1.6rem;
    @include w(466, 790);
    font-size: 2.4rem;
    margin-right: 23/790 * 100 + %;
    @include r(650) {
      width: 100%;
      text-align: center;
      order: 1; }
    &--right {
      order: 1;
      margin-right: 0;
      margin-left: 23/790 * 100 + %; } }
  &__title {
    font-size: 3.2rem;
    @include black;
    text-transform: uppercase; }
  &__text {
    padding-top: 1rem;
    @include regular;
    font-size: 1.6rem;
    color: #222;
    line-height: 2.1rem;
    @include r(650) {
      width: 100%; } }
  figure {
    @include w(300, 790);
    @include r(650) {
      width: 100%; } }
  &__img {
    width: 100%;
    display: block;
    background-color: #333; } }

.panel {
  background-color: #fff;
  position: relative;
  z-index: 99;
  &__skew {
    background-size: cover;
    background-position: center;
    background-repeat: repeat-y;
    box-shadow: inset 100vmax 0 0 rgba(0, 0, 0, .75);
    position: relative;
    transform: skew(0, 5deg) translateY(10rem); }
  &__content {
    padding-top: 13.5rem;
    transform: skew(0, -5deg);
    padding-bottom: 5.5rem;
    text-align: center;
    font-size: 3rem;
    @include w(808, 940);
    @include r(700) {
      width: 100%; }
    margin: 0 auto; }
  &__text {
    color: rgba(#fff, .8);
    font-family: "Myriad Pro", sans-serif;
    font-size: 3rem;
    line-height: 3.6rem; }
  &__btn {
    margin-top: 2.5rem;
    display: inline-block;
    @include bold;
    font-size: 2.4rem;
    color: #2ecc71;
    padding: 1.3rem 6.2rem;
    text-decoration: none;
    border: 3px solid #2ecc71;
    border-radius: 3rem;
    text-transform: uppercase;
    transition: .25s linear;
    &:hover {
      background-color: #2ecc71;
      color: #fff; } } }
.reviews {
  position: relative;
  z-index: 9;
  text-align: center;
  padding-top: 5rem;
  &__skew {
    padding-bottom: 15rem;
    background-color: #fff;
    transform: skew(0, -6deg) translateY(13rem); }
  &__content {
    transform: skew(0, 6deg); }
  &__review {
    margin-bottom: 7.5rem; }
  &__container {
    padding-top: 10rem;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center; }
  &__column {
    @include w(400, 940);
    @include r(768) {
      width: 100%; } } }


.review {
  display: flex;
  flex-direction: column;
  text-align: left;
  width: 100%;
  &__text {
    position: relative;
    @include regular;
    font-size: 1.6rem;
    line-height: 2.1rem;
    padding-bottom: 2rem;
    border-bottom: 3px solid #a1a1a1;
    &:after {
      content: '';
      position: absolute;
      width: 3px;
      height: 2rem;
      background-color: #a1a1a1;
      right: 0;
      bottom: 0; } }
  &__user {
    margin-left: auto;
    margin-top: 2rem; }
  &__user-photo {
    position: relative;
    vertical-align: middle;
    display: inline-block;
    width: 4.6rem;
    height: 4.6rem;
    background-size: cover;
    border-radius: 50%;
    background-position: center center;
    &:before {
      content: '';
      width: 3rem;
      position: absolute;
      left: -2.5rem;
      top: -2.7rem;
      height: 1.5rem;
      background-color: #fff;
      border-right: 3px solid #a1a1a1;
      border-bottom: 3px solid #a1a1a1;
      transform: rotate(45deg) skew(50deg); } }
  &__username {
    @include bold;
    display: inline-block;
    vertical-align: middle;
    font-size: 1.6rem;
    span {
      margin-right: 0;
      @include regular;
      font-size: 14/16+em; } } }
.footer {
  width: 100%;
  min-height: 700px;
  height: 100%;
  position: relative;
  @include r(768) {
    min-height: 1000px; }

  &__map {
    width: 100%;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    top: 0; }
  &__mask {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1;
    background-color: rgba(42, 50, 58, 0.9); }
  &__content {
    padding-top: 29rem;
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    height: 100%;
    @include r(768) {
      padding-top: 17rem; } }
  &__column {
    @include w(240, 940);
    text-align: left;
    border-right: 1px dotted #a1a1a1;
    @include r(768) {
      border: none;
      width: 70%;
      margin: 0 auto;
      text-align: center;
      padding-bottom: 5rem; }
    @include r(500) {
      width: 100%; }
    &:nth-child(2) {
      text-align: center;
      @include r(768) {
        order: 1; } }
    &--big {
      border: none;
      margin-left: auto;
      @include w(380, 940); } }
  &__logo {
    @include black;
    font-size: 2.4rem;
    text-transform: uppercase;
    color: #f39c12; }
  &__address {
    @include regular;
    font-size: 1.6rem;
    padding: 25px 0;
    color: #fff;
    font-style: normal; }
  &__contact {
    @include regular;
    span {
      display: block;
      color: #fff;
      text-transform: uppercase; }
    a {
      padding-left: .5em;
      display: inline-block;
      text-decoration: none;
      color: #2ecc71;
      text-transform: none; } }
  &__form {
    position: relative;
    padding-bottom: 30px; }
  &__btn-submit {
    background-color: transparent;
    position: absolute;
    display: inline-block;
    right: 0;
    top: 0;
    width: 15px;
    height: 15px;
    background-image: url(../images/i-arrow.svg);
    background-size: cover;
    background-position: center; }
  &__text {
    @include light;
    color: #fff;
    text-transform: uppercase;
    font-size: 1.6rem;
    line-height: 3rem; }

  &__copy {
    margin-top: auto;
    margin-bottom: 5rem;
    @include regular;
    font-size: 1.4rem;
    color: #fff;
    order: 2; } }
.input {
  display: inline-block;
  width: 100%;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #a1a1a1;
  padding-bottom: 5px;
  @include regular;
  color: #fff;
  &:placeholder {
    @include regular;
    text-transform: uppercase;
    color: rgba(1,1,1,.5);
    font-size: 1.6rem; } }

